













export default

  props:
    "sprite":
      type: Object
      required: true

  methods:
    processSprite: () ->
      url = process.env.VUE_APP_SERVER_ROOT + "/api/sprites/" + this.sprite.id + "/process/"

      options =
        method: "PUT"

      self = this
      fetch(url, options)
      .catch((error) ->
        console.log(error)
        )
      .then((response) ->
        response.json()
        )
      .then((json) ->
        self.$emit("sprite-process", self.sprite, json)
        )

