






















import Sprite from "./sprite"

export default

  props: ['display', 'sprite']

  components:
    "sprite": Sprite

  methods:
    deleteSprite: () ->
      url = "http://localhost:8000/api/sprites/#{this.sprite.id}/"
      options =
        method: "DELETE"

      self = this
      fetch(
        url,
        options
        )
      .catch((error) ->
        console.log error
        )
      .then((response) ->
        self.$emit('delete-sprite', self.sprite)
        )


