























import Sprite from "./sprite"
import NewSprite from "./newSprite"
import DeleteSprite from "./deleteSprite"

export default

	components:
		"sprite": Sprite
		"new-sprite": NewSprite
		"delete-sprite": DeleteSprite

	data: () ->
		sprites: []
		selectedSprite: null
		showConfirmDelete: false

	methods:
		fetchSprites: () ->
			url = "http://localhost:8000/api/sprites/"
			options=
				headers:
					"accept": "application/json"
			component = this
			fetch(url)
			.catch((error) ->
				console.log error)
			.then((response) ->
				console.log response
				response.json()
				)
			.then((json) ->
				console.log json
				component.sprites = json
				)

		confirmDelete: (sprite) ->
			this.selectedSprite = sprite
			this.showConfirmDelete = true

		hideDelete: () ->
			this.showConfirmDelete = false

		deleteSprite: (sprite) ->
			deleted = false
			index = 0
			until deleted
				if this.sprites[index].id == sprite.id
					this.sprites.splice(index, 1)
					deleted = true
				index++

			this.hideDelete()

		addNewSprite: (sprite) ->
			this.sprites.unshift(sprite)

	mounted: () ->
		this.fetchSprites()


