



























import ColorItem from "./colorItem"

export default

	components:
		"color-item": ColorItem

	props:
		["colorMap"]
	
	data: () ->
		selectedColor: null
	
	methods:
		colorRef: (color) ->
			"#{color.r},#{color.g},#{color.b}"

		selectColor: (color) ->
			this.$emit('select-color', color)
			this.selectedColor = this.colorRef(color)

		updateColor: (color) ->
			this.$emit('update-color', color)

