






































import Sprites from "./components/gallery/sprites"
import Editor from "./components/editor/editor"

export default

	components:
		"sprites": Sprites
		"editor": Editor

	data: () ->
		currentTab: "sprites"
		currentSprite: null

	methods:
		selectTab: (tab) ->
			this.currentTab = tab

		editSprite: (sprite) ->
			this.currentSprite = sprite
			this.currentTab = "editor"

