





























































import Sprite2d from "./sprite2d"
import Sprite3d from "./sprite3d"
import ProcessSprite from "./processSprite"

export default

	props:
		'sprite':
			type: Object
			required: true
		'displayTools':
			type: Boolean
			required: false
			default: true

	components:
		"sprite-2d": Sprite2d
		"sprite-3d": Sprite3d
		"process-sprite": ProcessSprite

	data: () ->
		renaming: false
		currentTab: "sprite-2d"

	methods:

		renamimgMode: () ->
			this.renaming = true

		renameSprite: () ->
			url = "http://localhost:8000/api/sprites/#{this.sprite.id}/"
			options =
				method: "PATCH"
				headers:
					"content-type": "application/json"
				body:
					JSON.stringify({"name": "#{this.sprite.name}"})

			self = this
			fetch(
				url,
				options
				)
			.catch((error) ->
				console.log error
				)
			.then((response) ->
				response.json()
				)
			.then((json) ->
					self.sprite.name = json.name
					self.renaming = false
				)

		handleSpriteProcess: (sprite, processResponse) ->
			console.log(processResponse)
			sprite.colorMap = processResponse.colorMap

